import { configureStore } from '@reduxjs/toolkit';
import counterSlice from './counterSlice';
import { persistStore, persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage'; //  sessionStorage

// 
const persistConfig = {
    key: 'root',
    storage,
    blacklist: ['someTemporaryReducer'], //  reducer blacklist
    transform: [
        (state) => JSON.stringify(state), // 
        (serializedState) => JSON.parse(serializedState) // 
    ]
};

// const rootReducer = (state = {}, action) => {
//     switch (action.type) {
//       // ... reducer 
//       default:
//         return state;
//     }
//   };

// console.log(counterSlice());
//  reducer
const persistedReducer = persistReducer(persistConfig, counterSlice);

//  Redux store
// const store = configureStore({
//   reducer: {
//     ...persistedReducer
//   }
// });


  //  Redux store
const store = configureStore({
    reducer: {
        counter: persistedReducer,
    },
    middleware: (getDefaultMiddleware) =>
        getDefaultMiddleware({
            serializableCheck: {
                //  action 
                ignoredActions: ['persist/PERSIST', 'persist/REHYDRATE'],
                //  state 
                ignoredPaths: ['counter.someNonSerializableField'],
            },
        }),
});



//  store
const persistor = persistStore(store);

export {
    persistor,
    store
}


export default store